.course-heading{
    font-size: 40px;
    font-weight: 400;
    margin-top: -25px;
    margin-left: 60px;
    margin-bottom: -30px;
    
}
.card-container{
    display: flex;
    flex-wrap: wrap;
    margin-left: 20rem;
    margin-top: 2rem;
    z-index: -2;
    
}
.card-link{
    text-decoration: none;
}
.card{
    width: 18rem;
    margin: 20px;
    border: 2px solid #969494;
    padding: 20px;
    cursor: pointer;
    border-radius: 10px;
    padding-top: 0;
    box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.5);
    
}
.card .title{
    font-size: 20px;
    font-weight: 520;
  font-family: "Inter-Regular", Helvetica;
color: #2e2d2d;

}
.university{
    display: flex;
    align-items: center;
    gap: 13px;  
    color: #615f5f;
}
.university p{
    
  font-family: "Inter-Regular", Helvetica;
  font-size: 16px;
}
.year{
    display: flex;
    align-items: center;
    gap: 13px;  
    color: #615f5f;  
}